@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', serif;
}
.header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  background: #f7f7f7ff;
  height: 8rem;
  z-index: 10;
}

.header__logo img {
  width: 170px;
  margin-top: 1.2rem;
  margin-bottom: 1.2rem;
  margin-left: 1.5rem;
}

.header__navitems ul {
  position: -webkit-sticky;
  position: sticky;
  width: 100%;
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  text-align: center;
}

.header__navitems li {
  width: 100%;
  height: auto;
  font-weight: lighter;
}

.header__navitems a {
  color: #595959;
  text-decoration: none;
  font-size: 1.3rem;
  height: auto;
  padding-top: 0.35rem;
  padding-bottom: 0.35rem;
  display: block;
  border-left: 1px solid #595959;
  border-top: 1px solid #595959;
  border-bottom: 1px solid #595959;
  background: #f3ede6ff;
}

.header__navitems li:last-child {
  border-right: 1px solid #595959;
}

li.link__wrapper a:hover {
  color: #ffffff;
  background: #fba100ff;
  text-decoration: none;
  font-weight: normal;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.navbar {
