  text-decoration: none;
  font-weight: normal;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.footer {
  color: #5e6d8a;
  background-color: #f7f7f7ff;
  position: relative;
  padding-top: 1.5rem;
  bottom: 0;
}

.footer__content {
  height: 6rem;
